@include font('Circe-Bold', 'Circe-Bold');
@include font('Circe-ExtraBold', 'Circe-ExtraBold');
@include font('Circe-Light', 'Circe-Light');
@include font('OpenSans-Light', 'OpenSans-Light');
@include font('OpenSans-Regular', 'OpenSans-Regular');
@include font('OpenSans-Bold', 'OpenSans-Bold');

@mixin c-bold {
	font-family: Circe-Bold, Arial, Helvetica, sans-serif; }
@mixin c-extrBold {
	font-family:  Circe-ExtraBold, Arial, Helvetica, sans-serif; }
@mixin c-reg {
	font-family:  Circe-Regular, Arial, Helvetica, sans-serif; }
@mixin c-light {
	font-family:  Circe-Light, Arial, Helvetica, sans-serif; }
@mixin o-light {
	font-family:  OpenSans-Light, Arial, Helvetica, sans-serif; }
@mixin o-req {
	font-family:  OpenSans-Regular, Arial, Helvetica, sans-serif; }
@mixin o-bold {
	font-family:  OpenSans-Bold, Arial, Helvetica, sans-serif; }
